
/* Global styles */
.app {
    font-family: Arial, sans-serif;
    min-height: 100vh;
    background-color: #f8f9fa;
  }
  
  .logout-button {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 1000;
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .sidebar {
      height: auto;
      border-right: none;
      border-bottom: 1px solid #dee2e6;
    }
  }