/* DogCard component styles */
.dog-card {
  border: 1px solid #dee2e6;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
}

.dog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.dog-card img {
  height: 200px;
  object-fit: cover;
}

.dog-card .card-body {
  padding: 15px;
}

.dog-card .card-title {
  font-size: 1.25rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.dog-card .card-text {
  font-size: 0.9rem;
  color: #555;
}

.dog-card .btn-link {
  color: #dc3545;
  padding: 0;
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-size: 30px;
}

.dog-card .btn-link:hover {
  text-decoration: none;
  color: #c82333;
}