/* Import Bootstrap */
@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.3.0/dist/css/bootstrap.min.css');

/* Background */
.sch_container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(135deg, #EEEEFF, #9FB4C7);
  overflow: hidden;
}

/* Sidebar */
.sch_sidebar {
  width: 100%;
  max-width: 300px;
  background: #321C4E;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 3px solid #7F7CAF;
  box-shadow: 3px 0 15px rgba(0, 0, 0, 0.2);
}

@media (max-width: 768px) {
  .sch_sidebar {
    width: 100%;
    max-width: none;
    border-right: none;
  }
}

/* Title */
.sch_title {
  font-size: 22px;
  font-weight: bold;
  color: #EEEEFF;
  margin-bottom: 20px;
  text-align: center;
}

/* Filters */
.sch_filter {
  width: 100%;
  background: #7F7CAF;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.sch_input {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
  border: none;
  font-size: 14px;
}

.sch_input:focus {
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
}

/* Buttons */
.sch_btn {
  background: #28587B;
  color: white;
  padding: 12px;
  font-size: 14px;
  border-radius: 8px;
  width: 100%;
  transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
}

.sch_btn:hover {
  background: #321C4E;
  transform: scale(1.05);
}

/* Dog Grid */
.sch_main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 20px;
}

.sch_dog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  width: 100%;
  max-width: 1200px;
  padding: 20px;
}

/* Pagination */
.sch_pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 20px;
}

.sch_page-btn {
  background: #28587B;
  color: white;
  padding: 10px 15px;
  font-size: 16px;
  border-radius: 8px;
  transition: 0.3s ease-in-out;
  border: none;
}

.sch_page-btn:hover {
  background: #321C4E;
  transform: scale(1.05);
}

.sch_page-info {
  font-size: 16px;
  font-weight: bold;
  color: #321C4E;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sch_container {
    flex-direction: column;
  }

  .sch_sidebar {
    width: 100%;
    max-width: none;
    border-right: none;
  }

  .sch_dog-grid {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  }

  .sch_pagination {
    flex-direction: column;
    gap: 10px;
  }

  .sch_btn, .sch_page-btn {
    width: 100%;
  }
}
