/* Login component styles */
.login-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f8f9fa;
  padding: 20px;
}

.login-container h1 {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
}

.login-container h2 {
  font-size: 1.75rem;
  color: #555;
  margin-bottom: 20px;
}

.login-container img {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

.login-container .form-control {
  margin-bottom: 15px;
}

.login-container .btn-primary {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
}

.login-container .error-message {
  color: #dc3545;
  font-size: 0.9rem;
  margin-top: 10px;
}